body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background: #757575;
}
html,
body{
  height: 100%;
}
#root{
  width: 100%;
  height: 100%;
  margin: auto;
}
#root:not(:empty)+.loader{
  display: none;
}
.loader{
  position: fixed;
  display: block;
  left: 45%;
  top: 45%;
  z-index: 9999;
  background: #FB8C00;
  border-radius: 50%;
  width: 122px;
  height: 122px;
  -webkit-box-shadow: 0 0 0 0 rgba(255, 87, 34, 0.6);
          box-shadow: 0 0 0 0 rgba(255, 87, 34, 0.6);
  animation: pulse 1s ease-in-out infinite;
  -webkit-animation: pulse 1s ease-in-out infinite;
}
.logo{
  position: fixed;
  top: 51.3%;
  left:46%;
  font-size: 20px;
  font-weight: bolder;
  color: #ffffff;
}
@keyframes pulse {
  0% { 
    -webkit-box-shadow: 0 0 0 0 rgba(255, 87, 34, 0.6); 
            box-shadow: 0 0 0 0 rgba(255, 87, 34, 0.6); 
    -moz-box-shadow:0 0 0 0 rgba(255, 87, 34, 0.6);
  }
  70% {
    -webkit-box-shadow: 0 0 0 100px rgba(255, 87, 34, 0);
            box-shadow: 0 0 0 100px rgba(255, 87, 34, 0);
    -moz-box-shadow:0 0 0 100px rgba(255, 87, 34, 0);
  }
  100% { 
  -webkit-box-shadow: 0 0 0 0 rgba(255, 87, 34, 0); 
          box-shadow: 0 0 0 0 rgba(255, 87, 34, 0);
  -moz-box-shadow:0 0 0 0 rgba(255, 87, 34, 0);
}
}
@-webkit-keyframes pulse {
  0% { -webkit-box-shadow: 0 0 0 0 rgba(255, 87, 34, 0.6); }
  70% {-webkit-box-shadow: 0 0 0 100px rgba(255, 87, 34, 0); }
  100% { -webkit-box-shadow: 0 0 0 0 rgba(255, 87, 34, 0);}
}



